/*font-weight = 300/500/700*/

:root {
    --first-primary: rgb(255, 255, 255);
    --second-primary: rgb(0, 0, 0);
    --third-primary: rgba(190, 38, 76, 1);

    --first-secondary: rgb(200, 200, 200);
    --second-secondary: rgb(40, 46, 56);
    --third-secondary: rgba(190, 38, 76, 1);

    --first-tertiary: rgb(100, 100, 100);
    --second-tertiary: rgba(19, 22, 27, 1);
    --third-tertiary: rgba(190, 38, 76, 1);

    --index: calc(1vw + 1vh);
    --transition: 1.5s cubic-bezier(.2, .5, 0, 1)
}

@font-face {
    font-family: montserrat_f;
    src: url("./Fonts/MontserratAlternates-Medium.ttf");
}

*, *:before, *:after {
    padding: 0;
    margin: 0;
    box-sizing: border-box;

    text-decoration: none;
    color: unset;

    transition: var(--transition);
}

html {
    font-family: montserrat_f, sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    scroll-behavior: smooth;
    overflow-x: hidden;
}

body {
    display:flex;
    flex-direction:column;
    color: var(--second-primary);

    overflow-x: hidden;
    -webkit-font-smoothing: antialiased;

    min-height: 100vh;
    min-width: 100vw;

    background: var(--first-primary);
}

#main {
    display: flex;
    flex-direction: column;

    max-width: 100vw;
    min-height: 100vh;

    justify-content: space-between;

    overflow-x: hidden;
}

::-webkit-scrollbar {
    width: 0;
    height: .4rem;
    background-color: var(--first-primary);
}

::-webkit-scrollbar-thumb {
    background-color: var(--second-primary);
}


/*
<===== SCREENS =====>
*/
#courseScreen {
    display: flex;
}


/*
<===== HEADER =====>
*/
header {
    display: flex;
    position: absolute;
    flex-direction: row;

    justify-content: space-evenly;
    align-items: center;

    font-size: calc(var(--index));

    width: 100%;
    height: calc(var(--index) * 4);

    padding: calc(var(--index) * .5) calc(var(--index) * 1.25);

    background: none;
    color: var(--second-primary);

    border-bottom: 1px solid #cce9ff;
}

.header-item {
    display: flex;

    justify-content: space-around;
    align-items: center;

    width: 100%;
}

a[href='/'],
a[href='https://test-eng-school.sanity.studio/desk/course'],
a[href='/link2'],
a[href='/link3'] {
    scale: .9;
}

a[href='/']:hover,
a[href='https://test-eng-school.sanity.studio/desk/course']:hover,
a[href='/link2']:hover,
a[href='/link3']:hover {
    scale: 1;
}

a[href='/']:hover #to-home {
    margin: calc(var(--index) * .75) calc(var(--index) * 2) 0 calc(var(--index) * 2.5);
}

a[href='https://test-eng-school.sanity.studio/desk/course']:hover #to-link1 {
    margin: calc(var(--index) * .75) calc(var(--index) * 2) 0 calc(var(--index) * 2.5);
}

a[href='/link2']:hover #to-link2 {
    margin: calc(var(--index) * .75) calc(var(--index) * 2) 0 calc(var(--index) * 2.5);
}

a[href='/link3']:hover #to-link3 {
    margin: calc(var(--index) * .75) calc(var(--index) * 2) 0 calc(var(--index) * 2.5);
}

#to-home, #to-link1, #to-link2, #to-link3 {
    display: flex;
    position: absolute;

    justify-content: center;
    align-items: center;

    z-index: 1000;

    height: calc(var(--index) * 3);

    margin: calc(var(--index) * 1) calc(var(--index) * 2) 0 calc(var(--index) * 2);

    color: var(--second-primary);
}

.header-cloud {
    display: flex;
    width: auto;
    height: calc(var(--index) * 4);

    z-index: 999;
}

.header-cloud-path-1 {
    stroke: #cce9ff;
    stroke-width: calc(var(--index) * .5);
    fill: #f0f9fe;
    scale: .85;
}

.header-cloud-path-2 {
    stroke: #cce9ff;
    stroke-width: calc(var(--index) * .5);
    fill: #daedfc;
    scale: .9;
}

.header-cloud-path-3 {
    stroke: #cce9ff;
    stroke-width: calc(var(--index) * .5);
    fill: #cce9ff;
    scale: .95;
}

a[href='/']:hover .header-cloud-path-1,
a[href='/']:hover .header-cloud-path-2,
a[href='/']:hover .header-cloud-path-3,
a[href='https://test-eng-school.sanity.studio/desk/course']:hover .header-cloud-path-1,
a[href='https://test-eng-school.sanity.studio/desk/course']:hover .header-cloud-path-2,
a[href='https://test-eng-school.sanity.studio/desk/course']:hover .header-cloud-path-3,
a[href='/link2']:hover .header-cloud-path-1,
a[href='/link2']:hover .header-cloud-path-2,
a[href='/link2']:hover .header-cloud-path-3,
a[href='/link3']:hover .header-cloud-path-1,
a[href='/link3']:hover .header-cloud-path-2,
a[href='/link3']:hover .header-cloud-path-3{
    scale: 1;
}


/*
<===== FOOTER =====>
*/
footer {
    display: flex;
    flex-direction: row;

    justify-content: space-evenly;
    align-items: center;

    width: 100%;
    height: calc(var(--index) * 2.5);

    background: var(--second-primary);
    color: var(--first-primary);
}


/*
<===== BUTTONS =====>
*/
.active-btn {
    background: #cce9ff !important;
    color: var(--second-primary);

    transition-duration: 500ms;
}


/*
<===== FILTER =====>
*/
.courses-filter {
    display: flex;
    position: absolute;
    justify-content: center;
    align-content: center;

    width: calc(var(--index) * 12.5);
    height: auto;

    top: calc(var(--index) * 4);
    left: 0;

    border-radius: calc(var(--index) * .5);

    padding: calc(var(--index) * 1.5);
    /*padding: calc(var(--index) * .25);*/
}

.filter-inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;

    text-align: center;

    width: 100%;

    padding: calc(var(--index) * .25);

    border: calc(var(--index) * .1) solid #cce9ff;
    border-radius: calc(var(--index) * .5);
}

.filter-btns-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;

    width: 100%;
}

.courses-filter:hover .filter-inner {
    box-shadow: rgba(35, 35, 35, 0.25) 0px 13px 27px -5px, rgba(23, 23, 23, 0.75) 0px 8px 16px -8px;
}

.filter-btn {
    font-family: montserrat_f, sans-serif !important;
    font-size: calc(var(--index) * .5);
    width: auto;
    height: 2rem;

    background: #f0f9fe;

    border-radius: 0;

    scale: 1;

    border: 0;

    transition: 500ms !important;
}

.filter-btn:first-child {
    border-radius: calc(calc(var(--index) * .5) - calc(var(--index) * .25)) calc(calc(var(--index) * .5) - calc(var(--index) * .25)) 0 0;
}

.filter-btn:last-child {
    border-radius: 0 0 calc(calc(var(--index) * .5) - calc(var(--index) * .25)) calc(calc(var(--index) * .5) - calc(var(--index) * .25));
}

.filter-btn:hover {
    background: #cce9ff;
}

.key-word-container {
    font-family: montserrat_f, sans-serif !important;
    font-size: calc(var(--index) * .5);

    width: 100%;

    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-top: calc(var(--index) * 1);
    border: calc(var(--index) * .1) solid #cce9ff;
    border-radius: calc(var(--index) * .25);

    padding: calc(var(--index) * 0);

    background: none;
}

.key-word-container div {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    border-top: calc(var(--index) * .1) solid #cce9ff;
    border-left: calc(var(--index) * .1) solid #cce9ff;
    border-bottom: calc(var(--index) * .1) solid #cce9ff;
    border-right: none;

    background: #cce9ff;

    /*border-radius: calc(calc(var(--index) * .25) - calc(var(--index) * .2)) 0 0 calc(calc(var(--index) * .25) - calc(var(--index) * .2));*/

    width: 35%;
    margin: 0;
}

.key-word-container input {
    font-family: montserrat_f, sans-serif !important;
    font-size: calc(var(--index) * .5);

    border-top: calc(var(--index) * .1) solid #cce9ff;
    border-right: calc(var(--index) * .1) solid #cce9ff;
    border-bottom: calc(var(--index) * .1) solid #cce9ff;
    border-left: none;

    background: #fff;

    border-radius: 0 calc(var(--index) * .15) calc(var(--index) * .15) 0;

    display: flex;
    width: 65%;

    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 0;
}

.range-container {
    display: flex;
    flex-direction: column;

    justify-content: center;
    align-items: center;

    margin-top: calc(var(--index) * 1);
}

.stars-container {
    display: flex;
    flex-direction: row;
}

.range-star {
    color: #cce9ff;
}


/*
<===== CONTAINERS =====>
*/
.container {
    display: flex;
    align-items: center;
    flex-direction: column;

    max-width: 1500px;
    min-height: 100%;

    flex: 1;

    margin: calc(var(--index) * 7.5) auto;
}

.course-container {
    display: flex;
    flex-direction: column;

    width: 100%;
    height: 100%;

    justify-content: center;
    align-items: center;

    gap: calc(var(--index) * 4) 0;
}




/*
<===== COURSE-CARD =====>
*/
.course-card {
    display: flex;
    position: relative;

    width: calc(var(--index) * 15);
    height: calc(var(--index) * 15);

    flex-direction: column;

    border-radius: calc(var(--index) * .5);

    z-index: 999;

    /*box-shadow: rgba(35, 35, 35, 0.25) 0px 13px 27px -.25rem, rgba(23, 23, 23, 0.25) 0px 8px 16px -.5rem;*/
    box-shadow: rgba(35, 35, 35, 0.25) calc(var(--index) * 0) calc(var(--index) * .5) calc(var(--index) * .5) calc(var(--index) * -.1), rgba(23, 23, 23, 0.75) calc(var(--index) * 0) calc(var(--index) * .25) calc(var(--index) * .5) calc(var(--index) * -.25);

    transition: var(--transition);
}

.course-card:hover {
    width: calc(100% - calc(var(--index) * 12.5));

    padding: 0;

    /*box-shadow: rgba(35, 35, 35, 0.25) 0px 13px 27px 1rem, rgba(23, 23, 23, 0.75) 0px 8px 16px 0rem;*/
    box-shadow: rgba(35, 35, 35, 0.25) calc(var(--index) * 0) calc(var(--index) * .5) calc(var(--index) * .75) calc(var(--index) * 0), rgba(23, 23, 23, 0.75) calc(var(--index) * 0) calc(var(--index) * .25) calc(var(--index) * .75) calc(var(--index) * -.1);
}

.course-card:hover .course-name {
    margin-top: calc(var(--index) * 2.5);
    opacity: 0;
    scale: .75;
}

.course-card:active {
    scale: .99;
    box-shadow: rgba(35, 35, 35, 0.25) 0px 13px 27px -.25rem, rgba(23, 23, 23, 0.75) 0px 8px 16px -.5rem;
}

.course-card:last-child {
    margin-bottom: 0;
}

.card-picture {
    display: flex;
    position: absolute;

    z-index: 1000;

    width: 100%;
    height: 100%;

    padding: calc(var(--index) * .25);

    overflow: hidden;
}

.course-card:hover .card-picture {
    width: 100%;
    height: 50%;

    padding: 0;
}

.picture {
    display: flex;
    width: 100%;
    height: 100%;

    border-radius: calc((var(--index) * .5) - (var(--index) * .25));

    object-fit: cover;
    object-position: center center;
}

.course-card:hover .picture {
    height: 100%;
    border-radius: calc(var(--index) * .5) calc(var(--index) * .5) 0 0;
}

.course-description {
    display: flex;
    flex-direction: column;

    position: absolute;
    justify-content: space-evenly;
    align-items: center;

    overflow: hidden;

    padding: 0 calc(var(--index) * .25);
    letter-spacing: calc(var(--index) * .05);

    width: 100%;
    height: 50%;

    top: 25%;

    z-index: 999;
}

.course-card:hover .course-description {
    top: 50%;
}

.course-title {
    display: flex;
    justify-content: center;
    align-items: center;

    font-size: calc(var(--index) * 1.25);
    letter-spacing: calc(var(--index) * .25);

    z-index: 999;

    width: 30%;
}

.course-name {
    display: flex;
    position: absolute;

    justify-content: center;
    align-items: center;
    text-align: center;

    font-size: calc(var(--index) * 1.75);
    letter-spacing: calc(var(--index) * .5);

    width: 100%;

    z-index: 999;

    margin-top: calc(var(--index) * -2.25);
}

.course-complexity {
    display: flex;
    justify-content: center;
    align-items: center;

    font-size: calc(var(--index) * .75);

    width: auto;

    padding: 0;
}

.course-complexity i {
    font-size: calc(var(--index) * .5);
    margin: 0 calc(var(--index) * .25);
}

.item-description {
    display: flex;
    justify-content: center;
    align-items: center;

    font-size: calc(var(--index) * .75);
    margin-bottom: calc(var(--index) * .75);

    text-align: center;

    width: auto;
}

#homeScreen .item-description {
    margin-bottom: 0;
}

.course-card:hover .item-description {
    display: flex;
}

.course-content {
    display: flex;
    justify-content: right;
    align-items: center;

    font-size: calc(var(--index) * 1);

    width: 100%;

    padding: 0;
}


.frame-container {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 825px;

    /*margin: auto auto;*/
}

.course-frame {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;

    border-radius: 23px;
}

